import Vue from 'vue'
import VueLazyload from 'vue-lazyload'
import App from './App.vue'
import router from './router'
import store from './store'

Vue.use(VueLazyload)

router.afterEach((to,from,next) => {
    window.scrollTo(0,0);
})    //用於切換頁面時回到頂部，但有些情況希望不用回到頂部


Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
